@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
body {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #121212;
  font-size: 16px;
  line-height: 24px;
}

.btn,
.btn:visited {
  display: inline-block;
  background: #E4C1AE;
  color: #fff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  padding: 16px 40px;
  border-radius: 32px;
  transition: 0.3s all;
  text-decoration: none;
}
.btn:hover, .btn:focus, .btn.btn--active,
.btn:visited:hover,
.btn:visited:focus,
.btn:visited.btn--active {
  background: #d7a488;
}